<template>
  <div class="nav-bar-fixed">
    <div class="nav-bar">
      <div class="nav-bar-left-continer">
        <div class="icon-toggle" @click="toggle">
          <i :class="hideLeft ? 'el-icon-s-fold' : 'el-icon-s-unfold'"></i>
        </div>
        <el-breadcrumb class="breadcrumb-container" separator="/">
          <el-breadcrumb-item v-for="item in breadcrumbList" :key="item.id">
            <!-- :to="index === 1 ? { path: '/index' } : null" -->
            {{ item.title }}
          </el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="nav-bar-right-continer">
        <el-dropdown trigger="hover">
          <div class="avatar-box">
            <img :src="avatar" class="avatar-img" />
            <span class="avatar-name">{{name}}</span>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item divided>
              <span></span>
            </el-dropdown-item>
            <el-dropdown-item divided>
              <span @click="logout"></span>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <div class="tags-view-container">
      <el-tag
        v-for="item in tagsViewList"
        :key="item.id"
        class="tags-view-item"
        :effect="currentMenuItem.id === item.id ? 'dark' : 'light'"
        :closable="item.id !== 'm-1'"
        @close="closeTagsViewItem(item)"
        @click="switchCurrentMenuItem(item)"
        >{{ item.title }}</el-tag
      >
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  created() {
  },
  computed: {
    ...mapGetters({
      hideLeft: 'menu/getHideLeft',
      currentMenuItem: 'menu/getCurrentMenuItem',
      breadcrumbList: 'menu/getBreadcrumbList',
      tagsViewList: 'menu/getTagsViewList',
      name: 'name',
      avatar: 'avatar'
    })
  },
  methods: {
    toggle() {
      this.$store.commit('menu/setHideLeft', !this.hideLeft)
    },
    async logout() {
      await this.$store.dispatch('user/logout')
      this.$router.push(`/login?redirect=${this.$route.fullPath}`)
    },
    closeTagsViewItem(item) {
      this.$store.commit('menu/removeTagsViewItem', item)
      this.$router.push(this.currentMenuItem.path)
    },
    switchCurrentMenuItem(item) {
      this.$store.commit('menu/setBreadcrumbList', item)
      this.$store.commit('menu/setCurrentMenuItem', item)
      this.$router.push(item.path)
    }
  }
}
</script>
<style lang="less" scoped>
  .nav-bar-fixed {
    z-index: 999;
  }
  .tags-view-container {
    min-width: 1920px;
  }
</style>
