  resetMenuList(roles) {
    if (roles[0] !== 'admin') {
      this.menuList = this.allList.filter(item => {
        if (item.roles) {
          return item.roles.includes(roles[0])
        } else return true
      })
    } else {
      this.menuList = this.allList
    }
    store.commit('menu/setMenuList', this.menuList)
    store.commit('menu/reSetTagsView')
  }
